.reuse-prf {
    width: 100%;
    margin: 0;
}

.reuse-prf__user {
    position: relative;
    padding: 0;
    margin-top: 25px;
    background-color: rgba(67,125,163, 1);
    box-shadow: 0 0 1px  0 rgba(0,0,0, .5);
}

.reuse-prf__user--img {
    position: relative;
    padding: 5px 0;
}

.reuse-prf__user--img__wrapper {
    position: relative;
    width: 100px;
    height: 100px;
    background-color: #fff;
    box-shadow: 0px 5px 20px 2px rgba(0,0,0, 0.4);
    margin: 0 auto;
}

.reuse-prf__user--img__wrapper >img {
    display: block;
    width: 100px;
    height: 100px;
    background-color: rgba(233,235,242, .7);
    cursor: pointer;
    image-rendering: -webkit-optimize-contrast;
    image-rendering: crisp-edges;
    -o-object-position: 50% 50%;
    object-position: 50% 50%;
    -o-object-fit: fill;
    object-fit: fill;
}

@media (min-width: 530px) {

    .reuse-prf__user--img__wrapper {
        top: 5px;
        left: 20px;
        margin: 0;
    }
}

.reuse-prf__user--img__status {
    position: relative;
    font-weight: 300;
    font-size: .8125rem;
    width: 100%;
    top: 2px;
    color: #e9ebf2;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.reuse-prf__user--img__status--wrapper > div {
    position: absolute;
    width: 8px;
    height: 8px;
    border-radius: 4px;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
}

.reuse-prf__user--img__status--wrapper {
    position: relative;
    /* width: 100px; */
    margin: 0 auto;
    white-space: nowrap;
    padding-left: 20px;
}

.reuse-prf__user--img__status--on {
    background-color: #16cf27;
    box-shadow: 0px 2px 7.76px 0.24px rgba(0, 98, 126, 0.4);
}

.reuse-prf__user--img__status--off {
    background-color: #ff1600;
