    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    font-size: .5rem;
    border: 1px solid rgba(0,0,0, .3);
    top: 50%;
    transform: translateY(-50%);
    left: 5px;
}

.reuse-prf__user--det__opt--chat {
    background-color: rgba(0,0,0, .2);
